// pages/events.js (snippet)
<div className="grid sm:grid-cols-2 md:grid-cols-3 gap-8">
  {Array.isArray(events) && events.length > 0 ? (
    events.map((event) => (
      <div key={event.id} className="bg-gray-800 p-4 rounded-xl shadow hover:shadow-blue-500/30 transition">
        <img
          src={event.images?.[0]?.url || "/images/placeholder.jpg"}
          className="w-full h-48 object-cover rounded-lg"
          alt={event.name || "Event"}
        />
        <h2 className="text-xl font-semibold mt-4">{event.name}</h2>
        <p className="text-gray-400 mt-1">
          {event.dates?.start?.localDate || "Date not available"}
        </p>
        <Link href={`/events/${event.id}`}>
          <button className="mt-4 w-full bg-blue-600 py-2 rounded-lg hover:bg-blue-500 transition">
            View Details
          </button>
        </Link>
      </div>
    ))
  ) : (
    <div className="col-span-full text-gray-400">
      No events found. Try a different search or check server logs.
    </div>
  )}
</div>
